@mixin trans  ($px,$deg) {
	font-size: $px;
	transform: rotate($deg);
	position: absolute; }
.present-me {
	height: 670px;
	display: flex;
	align-items: center;
	position: relative;
	@media (max-width: 800px) {
		height: 700px; }
	@media (max-width: 600px) {
		height: 600px; }
	@media (max-width: 384px) {
		height: 600px; }
	& .elipse {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 814px;
		height: 630px;
		overflow: hidden;
		@media (max-width: 1366px) {
			right: -5%; }
		@media (max-width: 996px) {
			width: 650px;
			right: -10%; }
		@media (max-width: 800px) {
			display: none; }
		@media (max-width: 375px) {
			display: block;
			bottom: 250px;
			right: -8%; }
		& svg {
			display: block;
			width: 100%;
			height: 100%; }
		@media (max-width: 375px) {
			height: 50%;
			width: 120%; } }
	& .pic img {
		width: 435px;
		position: absolute;
		right: 15%;
		bottom: 0;
		@media (max-width: 996px) {
			width: 400px;
			right: 10%; }
		@media (max-width: 800px) {
			width: 300px;
			bottom: 250px;
			right: (300/1100)*100%; }
		@media (max-width: 600px) {
			width: 180px;
			right: (300/900)*100%; }
		@media (max-width: 375px) {
			right: 180/(375*2)*100%; } }
	& .about {
		display: flex;
		flex-direction: column;
		position: relative;
		height: 298px;
		@media (max-width: 1024px) {
			height: 245px; }
		@media (max-width: 800px) {
			align-self: flex-end; } }
	& .name {
		font-size: 80px;
		margin-bottom: 14px;
		font-weight: bold;
		@media (max-width: 1366px) {
			font-size: 54px; }
		@media (max-width: 1024px) {
			margin-bottom: 18px;
			font-size: 45px; }
		@media (max-width: 997px) {
			font-size: 32px; }
		@media (max-width: 800px) {
			font-size: 30px;
			margin: 0 auto 20px auto; } }
	& .description {
		font-size: 25px;
		margin-bottom: 0px;
		color: gray;
		@media (max-width: 1024px) {
			font-size: 18px; }
		@media (max-width: 997px) {
			font-size: 16px; }
		@media (max-width: 800px) {
			margin: 0 auto;
			font-size: 16px;
			width: 212px;
			text-align: center; } }
	& .animation {
		width: 0;
		height: 0; }
	& .transform_sharp {
		@include trans(60px,27deg);
		top: 24%;
		left: -9%;
		@media (max-width: 1024px) {
			top: 130%;
			left: 3%; }
		@media (max-width: 800px) {
			top: -70%;
			left: -20%; }
		@media (max-width: 600px) {
			left: 0%;
			font-size: 25px; }
		@media (max-width: 384px) {
			left: 0; }
		@media (max-width: 800px) {
			top: -90%; } }
	& .transform_bracket {
		@include trans(50px,25deg);
		top: -54%;
		left: -20%;
		@media (max-width: 1024px) {
			left: 6%; }
		@media (max-width: 800px) {
			top: -180%;
			left: -16%; }
		@media (max-width: 600px) {
			top: -130%;
			left: 10%;
			font-size: 30px; }
		@media (max-width: 600px) {
			top: -140%; } }
	& .transform_close-tag {
		@include trans(40px,-20deg);
		bottom: -47%;
		@media (max-width: 1024px) {
			bottom: 120%;
			left: 40%; }
		@media (max-width: 800px) {
			display: none; } }
	& .transform_close-tag_clone {
		display: none;
		@media (max-width: 1024px) {
			display: block;
			@include trans(27px,-20deg);
			position: absolute;
			bottom: -40%;
			left: 30%; }
		@media (max-width: 800px) {
			bottom: 190%;
			left: -10%; }
		@media (max-width: 600px) {
			bottom: 210%;
			left: 40%;
			font-size: 20px; }
		@media (max-width: 600px) {
			bottom: 220%; } }
	& .transform_div {
		@include trans(37px,22deg);
		bottom: 5%;
		left: 50%;
		@media (max-width: 1732px) {
			display: none; } }
	& .transform_div_clone {
		@include trans(29px,-17deg);
		top: -38%;
		left: 35%;
		@media (max-width: 1791px) {
			top: -55%; }
		@media (max-width: 1024px) {
			top: -80%;
			left: 80%; }
		@media (max-width: 997px) {
			top: -80%;
			left: 90%; }
		@media (max-width: 800px) {
			top: -192%;
			left: 90%;
			font-size: 20px; }
		@media (max-width: 600px) {
			top: -125%;
			left: 70%; }
		@media (max-width: 375px) {
			top: -135%; } } }
